@import "../../global.scss";

.testimonials{
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column; //down one by one
    align-items: center;
    justify-content: center;


    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
    }
    .container{
        width: 100%;
        height: 90%;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile{
            flex-direction: column;
           
        }

        .card{
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            justify-content: space-around;
            flex-direction: column;
            padding: 20px;
            transition: all 1s ease;

            @include mobile{
                height: 180px;
                margin: 8px 0;
            }
    

            &.featured{
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile{
                    width: 250px;
                    height: 180px;
                    margin: 1px;
                }
        
            }

            &:hover{
                transform: scale(1.1);
            }

           .top{
               display: flex;
               align-items: center;
               justify-content: center;

               img{
                   &.left,&.right{
                       height: 25px;
                   }
                   &.user{
                       height: 60px;
                       width: 60px;
                       border-radius: 50%;
                       object-fit: cover;
                       margin: 0 30px;

                       @include mobile{
                        width: 30px;
                        height: 30px;
                    }
            
                   }
               }
           }

           .center{
               padding: 10px;
               border-radius: 10px;
               background-color: rgb(250, 232, 232);

               @include mobile{
                font-size: 12px;
                padding-bottom: 4px;
                
            }
    
           }
           .bottom {
               display: flex;
               justify-content: center;
               align-items: center;
               flex-direction: column;

               h3{
                   margin-bottom: 5px;

                   @include mobile{
                    font-size: 14px;
                    
                }
               }
               h4{
                   color: rgb(150, 148, 148);

                   @include mobile{
                    font-size: 13px;
                    padding-bottom: 5px;
                }
               }
           }
        }
    }
}