@import "../../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: $maincolor;
    position: fixed;
    top: 0;
    right: -300px;//in the not active mode, the right bar shoul not be appear
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    //in the not active mode, the right bar shoul not be appear
    //do the same thing that we have done for the topbar
    &.active{
        right:0;
    }
    ul{
        margin:0 ;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 300;
        color: white;
        width: 60%;
        li{
            margin-bottom: 25px;
            a{
                font-size:inherit;
                color: inherit;
                text-decoration: none;
            }
            &:hover{
                font-weight: 400;
            }
        }
    }
}