@import "../../global.scss";

.contact{
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    

    h1{
        font-size: 50px;
        //color: $maincolor;
        color: white;

        @include mobile{
         font-size: 20px;
        }
    }
        
        .container{
            width: 100vw;
            height: 90%;
            display: flex;
            align-items: center;
            justify-content: center;


            @include mobile{
              flex-direction: column;
              height: 100vh;
            }

            .left {
                flex: 6;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

              
                h5{
                    font-size: 15px;
                    width: 500px;
                    text-align: center;
                    color: rgb(202, 199, 199);

                    @include mobile{
                      font-size: 10px;
                      width: 300px;
                    }
                }

                form {
                    width: 80%;
                    height: 70%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-around;

                    @include mobile{
                     width: 300px;
                    }

              
                    input {
                      width: 500px;
                      height: 50px;
                      font-size: 14px;
                      border: none;
                      margin-top: 20px;
                      background-color: rgb(177, 171, 171);
                      border-radius: 10px;
                      text-indent: 20px;
                      box-shadow: 0px 0px 15px -10px white;

                      @include mobile{
                        font-size: 10px;
                        height: 50px;
                        margin-top: 5px;
                        width: 300px;
                      }

                      &:focus {
                        outline: none;
                      }
                    }
              
                    textarea {
                      width: 500px;
                      height: 200px;
                      font-size: 14px;
                      margin-top: 20px;
                      border: none;
                      background-color: rgb(177, 171, 171);
                      border-radius: 10px;
                      text-indent: 20px;
                      box-shadow: 0px 0px 15px -10px white;

                      @include mobile{
                        font-size: 10px;
                        width: 300px;
                        height: 120px;
                        margin-top: 5px;
                      }

                      &:focus {
                        outline: none;
                      }
                    }
              
                    button {
                      width: 200px;
                      height: 40px;
                      margin: 20px;
                      color: white;
                      background-color: crimson;
                      border: none;
                      border-radius: 10px;
                      font-weight: 500;
                      cursor: pointer;
                      box-shadow: 0px 0px 15px -10px white;
                      transition: all 1s ease;

                      @include mobile{
                        height: 30px;
                        margin: 5px;
                      }
              
                      &:focus {
                        outline: none;
                      }
                      &:hover{
                        transform: scale(1.1);
                    }
                    }
              
                    span {
                      color: rgb(255, 255, 255);
                    }
                  }
            
              }
              .right{
                  flex:6;
                  display: flex;
                  flex-direction: column;

                

                  .itemContainer{
                    margin-left:30px ;
                    margin-bottom: 50px;
                    @include mobile{
                      margin-bottom: 15px;
                    }
                    h4{
                        margin-bottom: 10px;
                        font-size: 20px;
                        color: white;

                        @include mobile{
                          font-size: 10px;
                          margin-bottom: 0;
                        }
                      }

                    .icon{
                        font-size: 25px;
                        margin-right: 10px;
                        color: crimson;
                        @include mobile{
                          font-size: 20px;
                          
                        }
                        }

                        span{
                            font-size: 15px;
                            font-weight: 500;
                            color: rgb(202, 199, 199);

                            @include mobile{
                              font-size: 10px;
                              margin-bottom: 0;
                            }
                           }
                           .icon-social{
                            font-size: 35px;
                            margin-right: 15px;
                            color: crimson;
                            cursor: pointer;
                            transition: all 1s ease;

                            
                            &:hover{
                                transform: scale(1.2);
                            }
    

                           }
                    }
                
              }
        }
    }
