@import "../../global.scss";

.portfolio{
    background-color: rgb(250, 232, 232);
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size:50px;

        @include mobile{
            font-size: 20px;
        }
    }
    ul{
        margin:10px;
        padding:0;
        list-style: none;
        display: flex;


        @include mobile{
            flex-wrap: wrap;
            margin:10px 0;
            justify-content: center;
        }

    }
    .container{
        width:70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
           width: 100%;
        }

        .item{
            width:220px;
            height: 150px;
            border-bottom: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            border-radius: 20px;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                object-fit: cover;
                z-index: 1;
                width: 100%;
                height: 100%;
            }

            &:hover{
                background-color: $maincolor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}

